<template>
  <div class="mt-4 row p-2">
    <template v-if="view == 'card'">
      <pb-contact-card
        v-for="(contact, index) in contacts"
        :item="contact"
        :key="contact.id"
        :department-title="departmentTitle(contact.department, contacts[index - 1] && contacts[index - 1].department)"
        :view="view"
      />
    </template>

    <template v-if="view == 'table'">
      <pb-contact-table
        v-for="(contact, index) in contacts"
        :item="contact"
        :key="contact.id"
        :department-title="departmentTitle(contact.department, contacts[index - 1] && contacts[index - 1].department)"
      />
    </template>
  </div>
</template>

<script>
import { get } from 'vuex-pathify'
import PbContactCard from "@/components/PbContactCard"
import PbContactTable from "@/components/PbContactTable"

export default {
  name: "PbContacts",
  props: {
    view: {
      type: String,
      default: 'card',
    }
  },
  components: {
    PbContactCard,
    PbContactTable,
  },
  methods: {
    departmentTitle(curDep, oldDep = []) {
      for (let idx = 0; idx < curDep.length; idx++) {
        if (oldDep[idx] && oldDep[idx].slug == curDep[idx].slug) {
          continue
        } else {
          return curDep.slice(idx)
        }
      }
      return []
    },
  },
  setup() {
    return {
      ...get('phonebook/', ['contacts']),
    }
  },
}
</script>

