<template>
  <div
    :class="['search d-flex justify-content-end', { 'animate': animate }, { 'active': focusState }, { 'auto-hide': !focusState && searchAutoHide && filter }, { 'auto-show': focusState && searchAutoHide && filter }]"
  >
    <div v-if="searchAutoHide" class="btn-ctrl">
      <button
        type="button"
        class="btn btn-primary btn-search"
        @click="selectAll"
        title="Поиск контактов"
      >
        <i class="fa-solid fa-magnifying-glass"></i>
        <span>поиск</span>
      </button>
    </div>
    <div class="search-ctrl">
      <div
        style="position:relative; margin:0; padding:0; border:none;"
        @mouseover="hoverDiv = true"
        @mouseleave="hoverDiv = false"
      >
        <div v-if="showClear" @click="clear">
          <div
            class="fas fa-times"
            style="position: absolute; cursor: pointer; z-index: 0; font-size: 25px; color: rgb(170, 170, 170); top: 11.5px; right: 15px;"
          />
        </div>
        <input
          ref="search"
          type="text"
          class="form-control form-control-lg"
          placeholder="Быстрый поиск контактов"
          spellcheck="false"
          autocomplete="off"
          v-model="filter"
          @blur="setFocus(false)"
          @focus="setFocus(true)"
          @keydown.esc="focusState = false"
          @keydown.enter="focusState = false"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { get, sync, call } from 'vuex-pathify'

export default {
  name: 'PbSearch',
  data() {
    return {
      hoverDiv: false,
      focusState: false,
      animate: false,
    }
  },
  computed: {
    showClear() { return this.hoverDiv && this.filter.length > 0 }
  },
  mounted() {
    if (this.searchIntercept) {
      window.addEventListener("keydown", this.keySearch)
    }
    if (!this.filter)
      this.focusState = true
  },
  unmounted() {
    window.removeEventListener("keydown", this.keySearch)
  },
  methods: {
    clear() {
      this.focusState = true
      // this.filter = ''
      this.filterChange('')
    },
    selectAll() {
      this.$refs.search.setSelectionRange(0, this.filter.length)
      this.focusState = true
    },
    setFocus(state) {
      setTimeout(() => { this.focusState = state }, 200)
      // this.focusState = state
    },
    keySearch(e) {
      if (e.keyCode === 114 || (e.ctrlKey && e.keyCode === 70)) {
        e.preventDefault();
        this.focusState = true
        this.selectAll()
      }
    }
  },
  watch: {
    focusState(state) {
      this.animate = true
      if (state) {
        setTimeout(() => {
          this.$refs.search.focus()
          this.animate = false
        }, 200)
      } else {
        setTimeout(() => {
          this.$refs.search.blur()
          this.animate = false
        }, 200)
      }
    }
  },
  setup() {
    return {
      ...get('app/*'),
      ...sync('phonebook', ['filter']),
      ...call('phonebook', ['filterChange']),
    }
  },
}
</script>

<style scoped>
.search {
  position: -webkit-sticky;
  position: sticky;
  /* background-color: white; */
  z-index: 1000;
  top: 37px;
}
.search > div.search-ctrl {
  margin-top: 48px;
  margin-bottom: 8px;
  flex: 1 1;
  opacity: 1;
  pointer-events: all;
}
.search.auto-hide {
  margin-top: -56px;
  transition: margin 0.2s 0.2s;
}
.search.auto-show {
  transition: margin 0.2s;
}
.search.auto-hide > div.search-ctrl {
  margin-top: 0;
  /* padding: 0; */
  opacity: 0;
  pointer-events: none;
  width: 30px;
  flex: none;
  transition: flex 0.2s, margin 0.2s 0.2s, opacity 0.2s ease;
}
.search.auto-show > div.search-ctrl {
  transition: flex 0.2s 0.2s, margin 0.2s, opacity 0.2s 0.2s ease;
}
.search > div.btn-ctrl {
  position: absolute;
  margin-top: 48px;
  opacity: 0;
  margin-right: 130px;
  pointer-events: none;
}
.search > div.btn-ctrl button {
  width: auto;
  background-color: white;
  color: #337ab7;
  border: 2px solid #337ab7;
}
.search > div.btn-ctrl span {
  font-weight: 600;
  margin-left: 10px;
  text-transform: uppercase;
}
.search.auto-hide > div.btn-ctrl {
  opacity: 0.8;
  margin-right: 10px;
  pointer-events: all;
  display: block;
  transition: margin 0.2s, opacity 0.2s ease;
}
.search.auto-show > div.btn-ctrl {
  transition: margin 0.2s 0.2s, opacity 0.2s 0.2s ease;
}
.search.auto-hide > div.btn-ctrl:hover {
  opacity: 1;
  transition: opacity 0.2s ease;
}
.search.animate > div.btn-ctrl {
  display: block !important;
}
.btn-search {
  width: 50px;
  height: 50px;
  padding: 10px 16px;
  font-size: 18px;
  line-height: 1.33;
  border-radius: 25px;
  background-color: #337ab7;
  color: white;
}
</style>