<template>
  <div v-if="apiService">
    <pb-search />
    <div :class="['contacts', { 'loading': loading && !loadingShow }]">
      <pb-detail-contact />
      <pb-contacts :view="defaultView" />
    </div>

    <div ref="observer" class="observer">
      <scale-loader
        :loading="loadingShow"
        color="#337ab7"
        height="20px"
        width="4px"
        margin="2px"
        radius="0px"
      ></scale-loader>
    </div>
  </div>
  <div v-else class="text-center" style="margin-top: 100px">
    <h1 class="display-4">Сервис временно недоступен :-(</h1>
    <p class="lead">Загляните к нам чуть позднее. Мы его обязательно починим!</p>
  </div>
</template>

<script>
import { useMeta } from 'vue-meta'
import { config } from '@/plugins'
import { get, call } from 'vuex-pathify'
import PbSearch from '@/components/PbSearch'
import PbDetailContact from '@/components/PbDetailContact'
import PbContacts from '@/components/PbContacts'
import ScaleLoader from 'vue-spinner/src/ScaleLoader'

export default {
  name: 'ContactsView',
  components: {
    PbSearch,
    PbDetailContact,
    PbContacts,
    ScaleLoader,
  },
  mounted() {
    const options = {
      rootMargin: '0px',
      threshold: 1.0
    }
    const callback = (entries, observer) => {
      if (entries[0].isIntersecting && !this.loading) {
        this.loadContacts()
      }
    }
    const observer = new IntersectionObserver(callback, options)
    observer.observe(this.$refs.observer)
  },
  methods: {
    ...call('phonebook', ['loadContacts']),
  },
  setup() {
    useMeta({
      title: config.title("Контакты"),
    })
    return {
      ...get('app/*'),
      ...get('phonebook/', ['apiService', 'loading', 'loadingShow']),
    }
  },
}
</script>

<style scoped>
.observer {
  height: 30px;
}
.contacts {
  opacity: 1;
  transition: opacity 0.6s;
}
.contacts.loading {
  opacity: 0;
}
</style>