<template>
  <template v-if="departmentTitle.length > 0">
    <pb-contact-department :department="departmentTitle" />

    <div class="row py-2 mx-0 px-0">
      <div class="col text-center">
        <h6>
          <small>Должность / Ф.И.О.</small>
        </h6>
      </div>
      <div class="col text-center">
        <h6>
          <small>Телефон внутренний</small>
        </h6>
      </div>
      <div class="col text-center">
        <h6>
          <small>Телефон городской</small>
        </h6>
      </div>
      <div class="col text-center">
        <h6>
          <small>HiCom</small>
        </h6>
      </div>
      <div class="col text-center">
        <h6>
          <small>Электронная почта</small>
        </h6>
      </div>
    </div>
  </template>

  <div class="row py-2 mx-0 px-0 click align-items-center" @click="clickContact">
    <small class="col">
      <div class="text-center text-sm-start">
        <span v-text="item.title" />
        <br />
        <span v-text="item.name" />
      </div>
    </small>
    <div class="col">
      <div class="justify-content-center">
        <pb-contact-value
          v-for="phone in extPhone(item.params.phone)"
          :key="phone"
          :item="phone"
          :notitle="uniqItem(extPhone(item.params.phone).map(p => p.type)).length == 1"
          small
          center
          hint
        />
      </div>
    </div>
    <div class="col">
      <div class="justify-content-center">
        <pb-contact-value
          v-for="phone in intPhone(item.params.phone)"
          :key="phone"
          :item="phone"
          :notitle="uniqItem(intPhone(item.params.phone).map(p => p.type)).length == 1"
          small
          center
          hint
        />
      </div>
    </div>
    <div class="col">
      <div class="justify-content-center">
        <pb-contact-value
          v-for="phone in hicomPhone(item.params.phone)"
          :key="phone"
          :item="phone"
          :notitle="uniqItem(hicomPhone(item.params.phone).map(p => p.type)).length == 1"
          small
          center
          hint
        />
      </div>
    </div>
    <div class="col">
      <div class="justify-content-center">
        <pb-contact-value
          v-for="email in item.params.email"
          :key="email"
          :item="email"
          :notitle="uniqItem(item.params.email.map(e => e.type)).length == 1"
          small
          center
          hint
        />
      </div>
    </div>
  </div>
</template>

<script>
import { get, sync, call } from 'vuex-pathify'
import PbContactValue from '@/components/PbContactValue'
import PbContactDepartment from '@/components/PbContactDepartment'
import PbContactPhoto from "@/components/PbContactPhoto"

export default {
  name: "PbContactTable",
  props: {
    item: Object,
    departmentTitle: {
      type: Array,
      default: [],
    },
  },
  components: {
    PbContactValue,
    PbContactDepartment,
    PbContactPhoto,
  },
  methods: {
    clickContact() {
      const selText = document.getSelection().toString()
      if (selText.length > 0) {
        return
      }
      // this.filter = this.item.slug
      this.filterChange(this.item.slug)
    },
    extPhone(phones) {
      return phones.filter((phone) => RegExp('^рабочий|^конференция', 'i').test(phone.type))
    },
    intPhone(phones) {
      return phones.filter((phone) => RegExp('^городской', 'i').test(phone.type))
    },
    hicomPhone(phones) {
      return phones.filter((phone) => RegExp('^hicom', 'i').test(phone.type))
    },
    uniqItem(items) {
      return items.filter((value, index, self) => self.indexOf(value) === index)
    },
  },
  setup() {
    return {
      ...get('app/*'),
      ...sync('phonebook/', ['filter']),
      ...call('phonebook', ['filterChange']),
    }
  },
}
</script>

<style scoped>
.click {
  cursor: pointer;
}
.row.click {
  border-top: 1px solid #dde4e4;
}
.row.click:hover {
  background-color: #f5f9fa;
}
</style>