<template>
  <div v-if="contact" class="mt-4">
    <div class="p-3 shadow-sm bg-white rounded d-flex flex-fill flex-row align-self-stretch">
      <div class="d-flex flex-column flex-fill">
        <div class="d-flex flex-column flex-sm-row">
          <div class="align-self-top text-center me-0 me-sm-4">
            <pb-contact-photo :src="contact.photo" />
            <!-- <nav class="nav justify-content-center">
              <a
                class="nav-link"
                target="_blank"
                href="http://10.10.2.250:3000/api/v1/contacts/66.vcf"
              >
                <i class="fas fa-address-card"></i>
              </a>
              <a
                class="nav-link"
                target="_blank"
                href="http://10.10.2.250:3000/api/v1/contacts/66.png"
              >
                <i class="fas fa-qrcode"></i>
              </a>
            </nav>-->
          </div>
          <div class="p-2 flex-fill text-center text-sm-start">
            <h3 v-text="contact.name" />
            <span>
              <p v-text="contact.title" />
              <div class="mt-3">
                <nav aria-label="breadcrumb">
                  <ol class="breadcrumb bg-light justify-content-center justify-content-sm-start">
                    <li v-for="(dep, idx) in contact.department" :key="dep" class="breadcrumb-item">
                      <a class="click" v-text="dep.name" @click="() => clickDepartment(idx + 1)" />
                    </li>
                  </ol>
                </nav>
              </div>
            </span>
            <div class="mt-4 row text-muted">
              <div class="d-flex flex-column col align-items-stretch m-2">
                <h6 class="m-0 pb-2" style="border-bottom: 1px solid #dee2e6">
                  <span class="fa fa-phone"></span>
                  <span class="ms-3">Телефон для связи:</span>
                </h6>
                <div class="flex-fill py-2">
                  <pb-contact-value
                    v-for="phone in contact.params.phone"
                    :item="phone"
                    :key="phone"
                  />
                </div>
              </div>
              <div class="d-flex flex-column col align-items-stretch m-2">
                <h6 class="m-0 pb-2" style="border-bottom: 1px solid #dee2e6">
                  <span class="fa fa-at"></span>
                  <span class="ms-3">Электронная почта:</span>
                </h6>
                <div class="flex-fill py-2">
                  <pb-contact-value
                    v-for="email in contact.params.email"
                    :item="email"
                    :key="email"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { get, sync, call } from 'vuex-pathify'
import PbContactPhoto from "@/components/PbContactPhoto"
import PbContactValue from '@/components/PbContactValue'

export default {
  name: "PbDetailContact",
  components: {
    PbContactPhoto,
    PbContactValue,
  },
  methods: {
    clickDepartment(size) {
      if (this.clickDepartmentName && (this.clickDepartmentNameLast || size < this.contact.department.length)) {
        const selText = document.getSelection().toString()
        if (selText.length > 0)
          return

        // this.filter = this.department[size - 1].slug
        this.filterChange(this.contact.department[size - 1].slug)
      }
    }
  },
  setup() {
    return {
      ...get('app/*'),
      ...get('phonebook/', ['contact']),
      ...sync('phonebook/', ['filter']),
      ...call('phonebook', ['filterChange']),
    }
  },
}
</script>

<style scoped>
ol.breadcrumb {
  padding: 0.75rem 1rem;
}
.click {
  cursor: pointer;
}
</style>