<template>
  <pb-contact-department :department="departmentTitle" />

  <div v-if="view == 'card'" class="col-lg-6 p-0 d-flex card-view">
    <div class="m-2 p-3 shadow-sm bg-white rounded d-flex flex-fill flex-row align-self-stretch">
      <div class="d-flex flex-column flex-fill">
        <div v-if="clipboardFastCopy" class="position-relative">
          <div class="btn-group btn-group-xs copy">
            <button
              type="button"
              class="btn btn-outline-phonebook"
              title="Копировать в буфер обмена"
              ref="copyBtn"
              @click="clipboardCopy"
            >
              <i class="fa fa-copy fa-lg"></i>
              <span v-text="copyText" />
            </button>
          </div>
        </div>
        <div class="d-flex flex-column flex-sm-row">
          <div class="align-self-top text-center pt-2">
            <pb-contact-photo :src="item.photo" @click.prevent="clickContact" />
          </div>
          <div class="p-2 flex-fill">
            <h6
              class="text-center text-sm-start click"
              v-text="item.name"
              @click.prevent="clickContact"
            />
            <small>
              <p class="text-center text-sm-start m-0" v-text="item.title" />
              <p class="text-center text-sm-start m-0" v-text="item.department.name" />
            </small>
          </div>
        </div>
        <div class="d-flex flex-row justify-content-around justify-content-sm-between text-muted">
          <div class="me-1" v-if="item.params.phone">
            <small class="text-nowrap">
              <span class="fa fa-phone"></span>
              <span class="font-weight-bold ms-1">Телефон для связи:</span>
            </small>
            <pb-contact-value v-for="phone in item.params.phone" :item="phone" :key="phone" small />
          </div>
          <div class="ms-1" v-if="item.params.email">
            <small class="text-nowrap">
              <span class="fa fa-at"></span>
              <span class="font-weight-bold ms-1">Электронная почта:</span>
            </small>
            <pb-contact-value v-for="email in item.params.email" :item="email" :key="email" small />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { get, sync, call } from 'vuex-pathify'
import PbContactValue from '@/components/PbContactValue'
import PbContactDepartment from '@/components/PbContactDepartment'
import PbContactPhoto from "@/components/PbContactPhoto"

import copy from 'copy-to-clipboard'

export default {
  name: "PbContactCard",
  props: {
    item: Object,
    departmentTitle: {
      type: Array,
      default: [],
    },
    view: {
      type: String,
      default: "card",
    }
  },
  data() {
    return {
      copyText: "копировать",
    }
  },
  components: {
    PbContactValue,
    PbContactDepartment,
    PbContactPhoto,
  },
  methods: {
    clickContact() {
      const selText = document.getSelection().toString()
      if (selText.length > 0) {
        return
      }
      // this.filter = this.item.slug
      this.filterChange(this.item.slug)
    },
    clipboardCopy() {
      this.copyText = "скопировано"
      // this.$refs.copyBtn.blur()
      setTimeout(() => {
        this.copyText = "копировать"
      }, 2000)
      let clipboard = []

      if (this.clipboardCopyItems_name)
        clipboard = clipboard.concat(this.item.name)
      if (this.clipboardCopyItems_title)
        clipboard = clipboard.concat(this.item.title)
      if (this.clipboardCopyItems_company)
        clipboard = clipboard.concat(this.item.department[0].name)
      if (this.clipboardCopyItems_department) {
        clipboard = clipboard.concat(this.item.department.slice(1).map(dep => dep.name))
      }

      copy(clipboard.join('\t'), { format: "text/plain" })
    }
  },
  setup() {
    return {
      ...get('app/*'),
      ...sync('phonebook/', ['filter']),
      ...call('phonebook', ['filterChange']),
    }
  },
}
</script>

<style scoped>
.click {
  cursor: pointer;
}
.copy {
  position: absolute;
  right: 0;
  display: none;
}
.card-view:hover .copy {
  display: block;
}
.btn-group-xs > .btn,
.btn-xs {
  padding: 0.1rem 0.5rem;
  font-size: 0.6rem;
  border-radius: 0.2rem;
}
.btn-outline-phonebook {
  opacity: 0.8;
  border-color: #337ab7;
  color: #337ab7;
  transition: opacity 0.2s ease;
}
.btn-outline-phonebook:hover {
  opacity: 1;
}
.btn > span {
  font-weight: 600;
  margin-left: 5px;
  text-transform: uppercase;
}
</style>