<template>
  <template v-if="department.length > 0">
    <div v-if="joinDepartmentName" class="col-12 p-0 d-flex">
      <div
        class="m-2 px-2 shadow-sm bg-light rounded d-flex flex-fill flex-column align-self-stretch"
      >
        <div
          class="m-2 d-lg-flex flex-row"
          v-for="(dep, idx) in department"
          :key="dep"
          @click="() => clickDepartment(idx + 1)"
        >
          <h5
            :class="['m-0 align-self-center flex-fill', { 'click': clickDepartmentName && (clickDepartmentNameLast || idx < department.length - 1) }]"
            v-text="dep.name + (idx < department.length - 1 && ' /' || '')"
          />
        </div>
      </div>
    </div>

    <template v-else>
      <div
        v-for="(dep, idx) in department"
        :key="dep"
        class="col-12 p-0 d-flex"
        @click="() => clickDepartment(idx + 1)"
      >
        <div
          class="m-2 p-2 shadow-sm bg-light rounded d-flex flex-fill flex-column align-self-stretch"
        >
          <div class="mx-2 d-lg-flex flex-row">
            <h5
              :class="['m-0 align-self-center flex-fill', { 'click': clickDepartmentName && (clickDepartmentNameLast || idx < department.length - 1) }]"
              v-text="dep.name"
            />
          </div>
        </div>
      </div>
    </template>
  </template>
</template>

<script>
import { get, sync, call } from 'vuex-pathify'

export default {
  name: "PbContactDepartment",
  props: {
    department: {
      type: Array,
      default: [],
    }
  },
  methods: {
    clickDepartment(size) {
      if (this.clickDepartmentName && (this.clickDepartmentNameLast || size < this.department.length)) {
        const selText = document.getSelection().toString()
        if (selText.length > 0)
          return

        // this.filter = this.department[size - 1].slug
        this.filterChange(this.department[size - 1].slug)
      }
    }
  },
  setup() {
    return {
      ...get('app/*'),
      ...sync('phonebook/', ['filter']),
      ...call('phonebook', ['filterChange']),
    }
  },
}
</script>

<style scoped>
.click {
  cursor: pointer;
}
</style>